import React from "react";
import { Link } from "react-router-dom";
import data from "../getProducts";

const Catalog = class extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      productFilter: "",
      filteredProducts: []
    };
    this.handleChange = this.handleChange.bind(this);
    this.filterProducts = this.filterProducts.bind(this);
  }

  componentDidMount() {
    data().then(result => {
      this.setState({ products: result });
      this.setState({ filteredProducts: result });
    });
  }

  handleChange (e) {
    this.setState({
      productFilter: e.target.value
    });
    this.filterProducts(event.target.value);
  };

  filterProducts(filter) {
    let filteredProducts = this.state.products;

    filteredProducts = filteredProducts.filter(product => {
      let productTitle = product.title.toLowerCase();
      return productTitle.includes(filter.toLowerCase());
    });

    this.setState({
      filteredProducts
    });
  }

  render() {
    const ProductImages = ({ images }) => {
      return (
        <Link to="#" className="positon-relative">
          <picture>
            <source media="(max-width: 560px)" srcSet={images[0].sm} />
            <source media="(min-width: 561px)" srcSet={images[0].sm} />
            <img className="img-fluid" src={images[0].original} alt="" />
          </picture>
          {images[1] ? (
            <div className="picture-hover">
              <picture>
                <source media="(max-width: 560px)" srcSet={images[1].sm} />
                <source media="(min-width: 561px)" srcSet={images[1].sm} />
                <img className="img-fluid" src={images[1].original} alt="" />
              </picture>
            </div>
          ) : null}
        </Link>
      );
    };

    const Product = ({ product }) => {
      return (
        <div className="card">
          <ProductImages images={product.images} />
          <Link to="#">
            <div className="card-body">
              <p className="card-text">{product.title}</p>

              <small className="text-muted">
                {product.available ? (
                  <span className="font-weight-lighter">
                    {(product.price / 100).toFixed(2)} CAD
                  </span>
                ) : (
                  <span>Sold out</span>
                )}
              </small>
            </div>
          </Link>
        </div>
      );
    };

    const PoductList = ({ items }) => {
      return (
        <div className={"row"}>
          {items.map((product, key) => (
            <div className="col-6 col-md-4" key={key}>
              <Product product={product} />
            </div>
          ))}
        </div>
      );
    };

    return (
      <div className="container">
        <div className="row filter-bar">
          <div className="col-md-12">
            <div className="input-group">
              <input
                type="text"
                className="form-control"
                placeholder="Search products"
                value={this.state.productFilter}
                onChange={this.handleChange}
              />
              <div className="input-group-append">
                <span className="input-group-text">
                  <i className="fas fa-search"></i>
                </span>
              </div>
            </div>
          </div>
        </div>
        <PoductList items={this.state.filteredProducts} />
      </div>
    );
  }
};

export default Catalog;
